const PaymentService = require('../services/PaymentService');
const asyncHandler = require('express-async-handler');

class PaymentController {
  constructor() {
    this.paymentService = new PaymentService();
  }

  // GET /api/payments - Listar pagamentos com paginação e filtros
  getAllPayments = asyncHandler(async (req, res) => {
    const { page, limit, ...filters } = req.query;
    const result = await this.paymentService.getAllPayments(page, limit, filters);
    res.json(result);
  });

  // GET /api/payments/:id - Obter pagamento específico
  getPaymentById = asyncHandler(async (req, res) => {
    const { id } = req.params;
    const payment = await this.paymentService.getPaymentById(id);
    res.json(payment);
  });

  // POST /api/payments - Criar novo pagamento
  createPayment = asyncHandler(async (req, res) => {
    const payment = await this.paymentService.createPayment(req.body);
    res.status(201).json(payment);
  });

  // PUT /api/payments/:id - Atualizar pagamento
  updatePayment = asyncHandler(async (req, res) => {
    const { id } = req.params;
    const payment = await this.paymentService.updatePayment(id, req.body);
    res.json(payment);
  });

  // DELETE /api/payments/:id - Excluir pagamento
  deletePayment = asyncHandler(async (req, res) => {
    const { id } = req.params;
    const result = await this.paymentService.deletePayment(id);
    res.json(result);
  });

  // PATCH /api/payments/:id/status - Atualizar status do pagamento
  updatePaymentStatus = asyncHandler(async (req, res) => {
    const { id } = req.params;
    const { status } = req.body;
    const payment = await this.paymentService.updatePaymentStatus(id, status);
    res.json(payment);
  });

  // POST /api/payments/:id/confirm - Confirmar pagamento
  confirmPayment = asyncHandler(async (req, res) => {
    const { id } = req.params;
    const { payment_date, payment_method, notes } = req.body;
    const payment = await this.paymentService.confirmPayment(id, {
      payment_date,
      payment_method,
      notes
    });
    res.json(payment);
  });

  // GET /api/payments/search/:term - Buscar pagamentos
  searchPayments = asyncHandler(async (req, res) => {
    const { term } = req.params;
    const { page, limit } = req.query;
    const result = await this.paymentService.searchPayments(term, page, limit);
    res.json(result);
  });

  // GET /api/payments/overdue - Obter pagamentos em atraso
  getOverduePayments = asyncHandler(async (req, res) => {
    const { page, limit } = req.query;
    const result = await this.paymentService.getOverduePayments(page, limit);
    res.json(result);
  });

  // GET /api/payments/due-today - Obter pagamentos que vencem hoje
  getPaymentsDueToday = asyncHandler(async (req, res) => {
    const { page, limit } = req.query;
    const result = await this.paymentService.getPaymentsDueToday(page, limit);
    res.json(result);
  });

  // GET /api/payments/stats - Obter estatísticas de pagamentos
  getPaymentStats = asyncHandler(async (req, res) => {
    const { period } = req.query;
    const stats = await this.paymentService.getPaymentStats(period);
    res.json(stats);
  });

  // GET /api/payments/revenue-chart - Obter dados para gráfico de receita
  getRevenueChart = asyncHandler(async (req, res) => {
    const { period, groupBy } = req.query;
    const chartData = await this.paymentService.getRevenueChart(period, groupBy);
    res.json(chartData);
  });

  // POST /api/payments/bulk-update - Atualizar múltiplos pagamentos
  bulkUpdatePayments = asyncHandler(async (req, res) => {
    const { payment_ids, update_data } = req.body;
    const result = await this.paymentService.bulkUpdatePayments(payment_ids, update_data);
    res.json(result);
  });

  // GET /api/payments/export - Exportar pagamentos
  exportPayments = asyncHandler(async (req, res) => {
    const { format = 'csv', ...filters } = req.query;
    const exportData = await this.paymentService.exportPayments(filters, format);
    
    res.setHeader('Content-Type', format === 'csv' ? 'text/csv' : 'application/json');
    res.setHeader('Content-Disposition', `attachment; filename=payments.${format}`);
    res.send(exportData);
  });

  // GET /api/payments/contract/:contractId - Obter pagamentos de um contrato
  getPaymentsByContract = asyncHandler(async (req, res) => {
    const { contractId } = req.params;
    const { page, limit } = req.query;
    const result = await this.paymentService.getPaymentsByContract(contractId, page, limit);
    res.json(result);
  });

  // GET /api/payments/client/:clientId - Obter pagamentos de um cliente
  getPaymentsByClient = asyncHandler(async (req, res) => {
    const { clientId } = req.params;
    const { page, limit } = req.query;
    const result = await this.paymentService.getPaymentsByClient(clientId, page, limit);
    res.json(result);
  });

  // POST /api/payments/generate-receipt/:id - Gerar recibo de pagamento
  generateReceipt = asyncHandler(async (req, res) => {
    const { id } = req.params;
    const receipt = await this.paymentService.generateReceipt(id);
    res.json(receipt);
  });

  // POST /api/payments/:id/send-reminder - Enviar lembrete de pagamento
  sendPaymentReminder = asyncHandler(async (req, res) => {
    const { id } = req.params;
    const { method = 'email' } = req.body;
    const result = await this.paymentService.sendPaymentReminder(id, method);
    res.json(result);
  });
}

module.exports = new PaymentController();